<template>
  <div class="position-relative w-100 overflow-hidden">
    <!-- secondary -->
    <img
      src="@/assets/imgs/version02/works-shape-secondary.svg"
      class="w-40 h-auto position-absolute object-position-left top-50 start-0"
      :class="$style.shapeSecondary"
      alt=""
    >
    <!-- primary -->
    <img
      src="@/assets/imgs/version02/works-shape-primary.svg"
      class="w-60 h-auto position-absolute object-position-left top-0 end-0"
      :class="$style.shapePrimary"
      alt=""
    >
    <!-- quaternary -->
    <img
      src="@/assets/imgs/version02/works-shape-quaternary.svg"
      class="w-40 h-auto position-absolute object-position-left bottom-0 end-0"
      :class="$style.shapeQuaternary"
      alt=""
    >
    <div class="position-relative container-xxl">
      <div class="row align-items-stretch pt-6 pb-5">
        <div class="col-12">
          <h3 class="fw-bold mb-2">
            Meet some projects I had participated in...
          </h3>
          <p class="text-tertiary mb-5">
            I usually work alone on front-end development, including website architecture, page design, front-end programming, and integration with backend APIs.<br>
            But there are also many experiences of working with other engineers, which allows me to quickly integrate into the team, and communicate efficiently with designers and backend engineers to ensure the smooth progress of the project.
          </p>
        </div>
        <div
          v-for="(item, index) in worksList"
          :key="index"
          class="js-show-on-scroll mb-5"
          :class="[worksStyleMap[item.id].isImportant ? 'col-12 col-md-6 col-lg-4' : 'col-12 col-md-6 col-lg-4']"
        >
          <div
            class="position-relative h-100 container-fluid g-0"
            :class="$style.work"
          >
            <div class="row align-items-stretch">
              <div :class="[worksStyleMap[item.id].isImportant ? 'col-12' : 'col-12']">
                <div class="bg-gray-light px-4 pt-4">
                  <div :class="$style.thumbnail">
                    <img
                      :src="require(`@/assets/imgs/works/${item.imgs[0]}`)"
                      alt=""
                      :class="$style.thumbnailImg"
                    >
                  </div>
                </div>
              </div>
              <div :class="[worksStyleMap[item.id].isImportant ? 'col-12' : 'col-12']">
                <div class="px-2 pt-4">
                  <div class="d-flex mb-2">
                    <h4 class="me-2">
                      {{ item.title }}
                    </h4>
                    <a
                      v-if="item.link"
                      :href="item.link"
                      target="_blank"
                      class="stretched-link"
                      :class="$style.link"
                    >
                      <AppIcon
                        name="link"
                        :class="$style.linkIcon"
                      />
                    </a>
                  </div>
                  <span
                    v-for="(tag, tagIndex) in item.tags"
                    :key="tagIndex"
                    :class="$style.tag"
                  >{{ tag }}</span>
                  <p class="text-tertiary user-select-none mb-3">
                    {{ item.description }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { worksList } from '@/helpers/useWorks'
const worksStyleMap = {
  itry: {
    isImportant: true
  },
  'wwooz-chaicy': {
    isImportant: true
  },
  green: {
    isImportant: true
  },
  'wwooz-shihyipaint': {
    isImportant: true
  },
  yunghsing: {
    isImportant: true
  },
  'wwooz-gooder': {
    isImportant: true
  },
  'wwooz-sheenc': {
    isImportant: true
  },
  keyview: {
    isImportant: false
  },
  'wwooz-song-you': {
    isImportant: false
  },
  'omniec-content-management': {
    isImportant: false
  },
  'omniec-frontend': {
    isImportant: false
  },
  'omniec-admin': {
    isImportant: false
  },
  'omniec-coolmama': {
    isImportant: false
  },
  'omniec-nana': {
    isImportant: false
  },
  'omniec-atenore': {
    isImportant: false
  }
}

