<style lang="scss" module>
  @import "~@/assets/styles/variables";

  .shapeSecondary {
    transform: translate(-10%, -20%);
  }

  .shapePrimary {
    transform: translate(20%, 15%);
  }

  .shapeQuaternary {
    transform: translate(-10%, 30%);
  }

  .work {
    transition: all 0.3s;

    &:hover {
      box-shadow: 0 0 0.75rem rgba($text, 0.2);
    }

    .thumbnail {
      position: relative;
      padding-bottom: 65%;
      height: 0;

      .thumbnailImg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top center;
      }
    }

    .link {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 2rem;
      height: 2rem;
      color: $quaternary-light;
      transition: color 0.3s;

      .linkIcon {
        width: 1.5rem;
        height: 1.5rem;
        fill: currentColor;
      }

      &:hover {
        color: $quaternary;
      }
    }

    .tag {
      display: inline-block;
      padding: map-get($spacers, 1) map-get($spacers, 2);
      margin: 0 map-get($spacers, 2) map-get($spacers, 3) 0;
      font-size: $small-font-size;
      background-color: rgba($tertiary, 0.25);
      user-select: none;
      border-radius: $border-radius;
    }
  }
</style>
